body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-width: 100vh;
}

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

article {
  position: relative;
  top: 50px;
}

.App-titres {
  width:100%;
  display: flex;
  justify-content: space-evenly;
  position: fixed;
  z-index: 2;
}

.App-titres div {
  width:100%;
  text-align:center;
	padding:5px;
  border:1px solid black;
  padding: 0.8em;
  font-weight: bold;
  background-color: rgb(97, 95, 95);
  color: aliceblue;
}

.App-titres div:nth-child(1) {
  width: 20%;
}

.App-titres div:nth-child(2) {
  width: 40%;
}

.App-titres div:nth-child(3) {
  width: 8%;
}

.App-titres div:nth-child(4) {
  width: 8%;
}

.App-titres div:nth-child(5) {
  width: 8%;
}

.App-titres div:nth-child(6) {
  width: 8%;
}

.App-titres div:nth-child(7) {
  width: 8%;
}

.App-titres-knowtobe{
  width:100%;
  display: flex;
  justify-content: space-evenly;
  position: fixed;
  z-index: 2;
}

.App-titres-knowtobe div:nth-child(1) {
  width: 60%;
}

.App-titres-knowtobe div:nth-child(2) {
  width: 10%;
}

.App-titres-knowtobe div:nth-child(3) {
  width: 10%;
}

.App-titres-knowtobe div:nth-child(4) {
  width: 10%;
}

.App-titres-knowtobe div:nth-child(5) {
  width: 10%;
}

.App-titres-knowtobe div {
  width:100%;
  text-align:center;
	padding:5px;
  border:1px solid black;
  padding: 0.8em;
  font-weight: bold;
  background-color: rgb(97, 95, 95);
  color: aliceblue;
}

.App-rows {
  width:100%;
  display: flex;
  justify-content: space-evenly;
}

.App-rows:hover {
  border: 2px solid #61dafb;
  box-shadow: 2px 2px 3px #61dafb, -2px -2px 3px #61dafb;
}

.App-rows-title {
  width: 100%;
  background-color: rgb(169, 169, 172);
  margin: 0;
  padding-top: 1em;
  padding-bottom: 1em;
  padding-left: 1em;
  text-align: left;
}

.App-rows div, label {
  width:100%;
  text-align:center;
	padding:5px;
  padding: 0.8em;
  border-right: 1px solid grey;
}

.App-rows:nth-child(odd) {
  background-color: #eee;
}

.App-rows div:nth-child(1) {
  width: 20%;
}

.App-rows div:nth-child(2) {
  width: 40%;
}

.App-rows div:nth-child(3) {
  width: 8%;
}

.App-rows label:nth-child(4) {
  width: 8%;
}

.App-rows label:nth-child(5) {
  width: 8%;
}

.App-rows label:nth-child(6) {
  width: 8%;
}

.App-rows label:nth-child(7) {
  width: 8%;
  border-right: 0;
}

.article-knowtobe {
  position: relative;
  top: 70px;
}

.App-rows-knowtobe {
  width:100%;
  display: flex;
  justify-content: space-evenly;
}

.App-rows-knowtobe:hover {
  border: 2px solid #61dafb;
  box-shadow: 2px 2px 3px #61dafb, -2px -2px 3px #61dafb;
}

.App-rows-knowtobe div, label {
  width:100%;
  text-align:center;
	padding:5px;
  padding: 0.8em;
  border-right: 1px solid grey;
}

.App-rows-knowtobe:nth-child(odd) {
  background-color: #eee;
}

.App-rows-knowtobe div:nth-child(1) {
  width: 60%;
  text-align:left;
}

.App-rows-knowtobe label:nth-child(2) {
  width: 10%;
}

.App-rows-knowtobe label:nth-child(3) {
  width: 10%;
}

.App-rows-knowtobe label:nth-child(4) {
  width: 10%;
}

.App-rows-knowtobe label:nth-child(5) {
  width: 10%;
}

#valider {
  width: 100%;
  height: 3em;
  color: grey;
  font-weight: bold;
  font-size: 1em;
  background-color: #eee;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border: 2px solid lightskyblue;
  border-radius: 5px;
}

#valider:hover {
  border: 3px solid #61dafb;
  color: #61dafb;
}

.results {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.App-results {
  width: 500px;
  height: auto;
  padding: 1.2em;
  border: 1px solid black;
  border-radius: 5px;
  background-color: #eee;
  font-size: 1.2em;
  font-weight: bold;
  text-align: left;
  margin: 1em 0 1em 2em;
  display: flex;
  flex-direction: column;
}

.App-results h3 {
  color: #1DA598;
  font-weight: bold;
}

.App-results h4 {
  margin: 0.5em 0;
}

.App-results button {
  width: 100%;
  align-content: center;
  padding: 0.7em;
  margin-top: 1em;
  border-radius: 4px;
  font-weight: bold;
  color: #282c34;
}

.App-results button:hover {
  border: 2px solid #61dafb;
  color: #61dafb;
}

.progress-bar {
  position: relative;
  height: 40px;
  width: 450px;
  border-radius: 50px;
  border: 1px solid #333;
  text-align: center;
  display: flex;
  align-items: center;
  align-content: center;
}

.filler {
  height: 100%;
  border-radius: inherit;
  -webkit-transition: width .2sec ease-in;
  transition: width .2sec ease-in;
}

.number {
  position: absolute;
  left: calc(350px / 2);
}

.closeResults {
  width: 30vw;
  height: auto;
  font-size: 1.2em;
  font-weight: bold;
  color: #282c34;
  padding: 0.7em 0.35em;
  border-radius: 7px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border: 2px solid #1DA598;
  cursor: pointer;
  margin-left: 2em;
}

.closeResults:hover {
  border: 4px solid #1DA598;
  color: #1DA598;
}
